import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/layout/index.vue'
import ProDetail from '../views/prodetail/index.vue'
import Home from '@/views/layout/home.vue'
import Search from '@/views/layout/search.vue'
import Setting from '@/views/layout/setting.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: '/home',
        name: 'home',
        component: Home
      },
      {
        path: '/search',
        name: 'search',
        component: Search
      },
      {
        path: '/setting',
        name: 'setting',
        component: Setting
      }
    ]
  },
  {
    path: '/prodetail/:id',
    name: 'detail',
    component: ProDetail
  }

]

const router = new VueRouter({
  routes
})

export default router
